<template lang="">
    <div class="_full_router personal-con">
      <custHeader :title="title"></custHeader>
      <div style="overflow:scroll;height:100vh">
        <div class="menu-list">
            <mt-cell title="当日成交" is-link  to="/dealQry">

            </mt-cell>
            <mt-cell title="当日委托"  is-link to="/upCltOrderQry">
            </mt-cell>
            <mt-cell title="历史成交"  is-link to="/dealQryHistroy">
            </mt-cell>
            <mt-cell title="历史委托"   is-link to="/upCltOrderQryHistroy"  > </mt-cell>

            <mt-cell title="资金流水"   is-link to="/info/fundQry"  > </mt-cell>
            <mt-cell title="充提流水"   is-link to="/info/chargeTakeQry"  > </mt-cell>

            <mt-cell title="产品流水"   is-link to="/info/stockQry"  > </mt-cell>

            <mt-cell title="冻结明细"   is-link to="/info/frzQry"  > </mt-cell>
            <mt-cell title="计提购物金"   is-link to="/info/accruedQry"  > </mt-cell>
        </div>
      </div>
        
    </div>

</template>
<script>
import custHeader from  'components/custHeader'
import cookieUtil from 'js-cookie'
import {MessageBox,Cell} from 'mint-ui'
import src1 from "assets/l1.png"
import src2 from "assets/l2.png"
Vue.component(Cell.name,Cell);
export default{
    data(){
        return {
            title:'查询中心',
            smrz:src1,
            fxpc:src2,
            authStatus:""
        }
    },
    created:function(){
        //  axiosUtil.isLogin().then(message=>{
        //     var message=message.data;
        //       if(message.errorcode==0){//是否登录
                        this.getRealnameAuthQry()
        //       }else{
        //         this.$router.push("/login");
        //       }
        //   })
    },
    components:{
      custHeader
    },
    methods:{
       getRealnameAuthQry:function(){
           axiosUtil.getRealnameAuthQry().then(data=>{
            var data=data.data;
            if(data.errorcode==0){
                if(data.data==0){
                    this.authStatus="未认证";
                }
            }
            })
       }
    }
}

</script>
<style scoped>
    .personal-con{
         height: 100%;
    background: #f1f1f1;
    }
.menu-list{
    height:100vh;
}
.menu-list >a{
    margin-top: 12px;
}

</style>



